import {createRouter, createWebHistory} from 'vue-router'

import MyAbout from '../views/about.vue'
import MyHome  from '../views/home.vue'
import MyContact  from '../views/contact.vue'
import StyleTransfer  from '../views/styletransfer.vue'
import MyPost1  from '../views/posts/post1.vue'

const routes = [
    {
        path: '/',
        name: 'home',
        component: MyHome
    },
    {
        path: '/about',
        name: 'about',
        component: MyAbout
    },
    {
        path: '/contact',
        name: 'contact',
        component: MyContact
    },
    {
        path: '/styleT',
        name: 'styletransfer',
        component: StyleTransfer
    },
    {
        path: '/post1',
        name: 'Mypost1',
        component: MyPost1
    },

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
        routes
})

export default router